import { Injectable } from '@angular/core';
import * as math from 'mathjs'; 
import {CutInSentenceService} from "../services/cut-in-sentence.service";
import {MiscOperationsService} from "../services/misc-operations.service";

@Injectable({
  providedIn: 'root'
})
export class LineSeparatorService {
  lineGroupNumber = 0;
  lineGroupArray: Array<number>=[];  
  currentContent='';
  twoOrThreeFlag=0;
  currentLineArray: Array<any>=[];  
  pageHeight=0;
  linesArray:Array<String>=[]; 
  sumLineHeights=0;
  currentPageIndex=0;
  firstIndex=0;
  lastIndex=0;
  maxTextWidth=0;
  linesPerPage=0;
  currentFontName='Verdana';
  currentPageNumber=0;

  constructor(
    private cutInSentenceService:CutInSentenceService,
    private miscOperationsService:MiscOperationsService
  ) { 
  }
  
  //create methods
  //splits the new text in lines and calls the createTextContent method.
  displayTextContent(textContent,maxTextWidth,newFontSize){
    var currentUrl=String(textContent);
    this.currentLineArray=this.cutInSentenceService.
    splitLines(currentUrl,maxTextWidth);    
    var firstLine=this.currentLineArray[0];
    this.linesPerPage=
    Math.floor(
      (this.pageHeight/
      this.cutInSentenceService.getLineHeight(firstLine)));
    //this.twoOrThreeFlag=1;
    return this.createTextContent(0);
  }
  //creates the html for the whole document.
  createTextContent(i){
    var divInit=this.createNewPageDiv(0);
               this.currentContent+=
               `<div id="divToZoom" 
               style="text-align:center;
               margin:2%;padding:2%;'+
               width:100%;height:100%">`+divInit;
               this.currentContent+='</div></div>';
               return this.currentContent; 
  }
  //creates the div/html for the new page.
  createNewPageDiv(pageNumber){
    return '<div id="page'+pageNumber+'" '+
    'style="margin-top:2%;padding:2%;overflow-x: hidden;'+
               'top:0%;width:100%;height:100%"><br><br>';
  }
//create
  //update methods
  loadTextContent(body,newText,isInit,maxTextWidth){
    //console.log('this.twoOrThreeFlag3= '+this.twoOrThreeFlag);
    this.cutInSentenceService.allLinesArray=[];
    this.maxTextWidth=maxTextWidth;
    this.currentLineArray=
    this.cutInSentenceService.splitLines(newText,maxTextWidth);    
    var firstLine=this.currentLineArray[0][0];
    var firstFactor=Math.floor(
      (this.pageHeight/
    this.cutInSentenceService.getLineHeight(firstLine))/1.5);
    //console.log('this.pageHeight= '+this.pageHeight);
    //console.log('this.cutInSentenceService.getLineHeight(firstLine))= '+
    //this.cutInSentenceService.getLineHeight(firstLine));
    
    var extraFontFactor=1.4;
    var extraLineDistanceFactor=1;
    var oldFont=this.cutInSentenceService.oldFontSize;
    var newFont=this.cutInSentenceService.neoFontSize;
    //var fontName=this.cutInSentenceService.fontName;
    //if(fontName=='OpenDyslexicMono-Regular'){
      //extraFontFactor=1;
    //}
    
    if(newFont<12){
      extraFontFactor=0.9;
    }else if(newFont>14){
      extraFontFactor=1.6;
    }else if(newFont>18){
      extraFontFactor=2;
    }
    var lineDistance=this.cutInSentenceService.currentLineHeight;
    if(lineDistance=='400%'){
      extraLineDistanceFactor=0.5;
    }else  if(lineDistance=='150%'){
      extraLineDistanceFactor=1.3;
    } 
    
    var fontSizeChangeFactor=Math.ceil(extraFontFactor*
      (oldFont/newFont));

    this.linesPerPage=20;
    //=Math.floor((firstFactor*fontSizeChangeFactor
      //*extraLineDistanceFactor)/2);

      this.cutInSentenceService.getLineHeight(firstLine);
      this.updateText(body,newText,isInit);
  //console.log('this.linesPerPage= '+this.linesPerPage);
    //console.log('this.currentPageIndex= '+this.currentPageIndex);
  }
  //adds more pages or replaces old ones.
  updateText(body,newText,isInit){
    //console.log('this.twoOrThreeFlag2= '+this.twoOrThreeFlag);
    this.currentPageIndex=0;
    this.addMorePages(body,0,500);
    this.registerEventsToAllPages(body,0);
    //if((this.currentPageNumber>0)&&
    //(this.currentPageNumber>this.currentPageIndex)){
      //this.removeRestPages();
      //console.log('addsmorepages1 this.currentPageIndex= '+
      //this.currentPageIndex);
      //console.log('addsmorepages2 this.currentPageNumber= '+
      //this.currentPageNumber);
      
      this.removeRemaindingPages(this.currentPageIndex,
        this.currentPageNumber);
    //}
    this.currentPageNumber=this.currentPageIndex;
    //this.setBordersToParagraphs();
  }
  removeRemaindingPages(oldPageIndex,newPageIndex){
      var divToZoom=document.getElementById('divToZoom');
      for(var i=(oldPageIndex);i<newPageIndex;i++){
        console.log('i= '+i);
        var subDivRest=<HTMLElement>divToZoom.children[i].children[0];
        //subDivRest.style.width='0';
        //subDivRest.style.height='0';
        //subDivRest.style.margin='0';
        //subDivRest.style.padding='0';
        //subDivRest.style.backgroundColor='red';
        
        subDivRest.remove();
        //divToZoom.removeChild(subDivRest);
        //divToZoom.style.backgroundColor='blue';
      }
  }
  //adds more pages or replaces old ones.
  addMorePages(body,currentIndex,numberOfPages){
    var totalLength=this.getTotalArrayLength();
    if((this.currentPageIndex<(numberOfPages+1))&&
    (currentIndex<totalLength)){
      this.addANewPage(currentIndex,this.currentPageIndex); 
      //this.lastIndex=this.createTextForNewPage(currentIndex,0);
      this.currentPageIndex++;  
      this.addMorePages(body,this.lastIndex,numberOfPages);
      this.registerEventsToAllPages(body,0);
    }else{
      this.removeRestPages();
    }
  }
  addPageInTheEnd(body,newNumberOfPages){
    var totalLength=this.getTotalArrayLength();
    if(this.lastIndex<(totalLength-1)){
      //this.addANewPage(this.lastIndex,this.currentPageIndex); 
      //this.lastIndex=this.createTextForNewPage(this.lastIndex,0);
      //this.currentPageIndex++;  
      this.addMorePages(body,this.lastIndex,newNumberOfPages);
      this.registerEventsToAllPages(body,this.lastIndex);
    }
  }
  addRemovePage(body){
    var divToZoom=document.getElementById('divToZoom');
    this.addMorePages(body,divToZoom.children.length,
      (divToZoom.children.length+1));
  }
  //on update it adds a new page or replaces the text content of an old one.
  addANewPage(currentIndex,pageIndexToInsert){
    var divToZoom=document.getElementById('divToZoom');
    var childDiv=null;
    var isOldPage=0;
    if(divToZoom.children.length>(this.currentPageIndex)){
      //updates an existing page div
      isOldPage=1;
      childDiv =<HTMLDivElement>divToZoom.children[this.currentPageIndex];
      //childDiv=document.getElementById('page'+this.currentPageIndex);
      this.setupChildDivs(childDiv);
      this.createTextForNewPage(currentIndex,isOldPage);
      childDiv.innerHTML =''+this.currentContent; 
    }else{
      //creates a new page div
      childDiv =document.createElement("div");
      childDiv.id='page'+pageIndexToInsert;//this.currentPageIndex;
      this.setupChildDivs(childDiv);
      this.createTextForNewPage(currentIndex,isOldPage);
      childDiv.innerHTML =''+this.currentContent; 
      //setTimeout(() => {
        divToZoom.appendChild(childDiv);
      //}, 500);
    }
    //currentIndex=
  }
  setupChildDivs(childDiv){
    var divToZoom=document.getElementById('divToZoom');
    childDiv.style.height='100%';
    childDiv.style.backgroundColor='white';
    childDiv.style.marginTop='2%';
    childDiv.style.padding='2%';
    childDiv.style.overflowX='hidden';
    childDiv.style.top='0%';
    childDiv.style.width='100%';
    childDiv.style.margin='0 auto';
    childDiv.style.marginBottom='2%';
    divToZoom.style.padding='5% yellow';
    
  }
  //creates the text/html content for the new page.
  createTextForNewPage(i,isOldPage){
    this.currentContent=
    '<div id="'+this.currentPageIndex+
    '" style="position:absolute;display:block;'+
    'line-height:'+this.cutInSentenceService.currentLineHeight+'" class="pageClass">';//page+this.currentPageIndex;
    var totalLength=this.getTotalArrayLength();
    var totalCounter=0;
    if(this.currentLineArray.length>0){
      var paragraphIndex=0;
      var linesPerPage=20;
      for(var k=i;k<(this.linesPerPage+i);k++){
        var line='';
        var sentenceInfo=this.findSentenceInParagraphArray(k);
        var bordersArray=this.miscOperationsService.bordersArray;
        var leftBorder=((bordersArray[0]==1)||(bordersArray[4]==1))?'solid green':'';
        var rightBorder=((bordersArray[1]==1)||(bordersArray[4]==1))?'solid red':'';
        var topBorder=((bordersArray[2]==1)||(bordersArray[4]==1))?'solid green':'';
        var bottomBorder=((bordersArray[3]==1)||(bordersArray[4]==1))?'solid red':'';
        
        if((k==i) || (sentenceInfo[1]==1)){
          this.currentContent+=`<div id="par`+paragraphIndex
          +`" style="border-left:`+leftBorder+`;`+
          `border-right:`+rightBorder+`;`+
          `border-top:`+topBorder+`;`+
          `border-bottom:`+bottomBorder+`;" >`;//style="background-color:red"
          paragraphIndex++;
        }
        if(k<=totalLength-1){
          line=''+sentenceInfo[0];//this.currentLineArray[k];
        }
        //this.twoOrThreeFlag=0;
        var currentColor='black';
        switch(this.twoOrThreeFlag){
          case 1:{
            //2 Colors
            console.log('blue1');
            currentColor='blue';
            if((i+1)%2==1)
            console.log('red1');
            currentColor='red';
              break;
          }
          case 2:{
            //3 Colors
            currentColor='blue';
            if((i+1)%3==1){
              currentColor='green';
            }
            else if((i+1)%3==2){
              currentColor='red';
            }
            break;
          } 
        }
        //this.sumLineHeights+=
        //this.cutInSentenceService.getLineHeight(line);
        this.currentContent+=`<span
        id="span`+k+`" class="spanClass" 
        style="color:`+currentColor+`;`+
        `font-family:`+this.currentFontName+`!important;`+
        `font-size:`+this.cutInSentenceService.neoFontSize
        //this.cutInSentenceService.fontSize
        +`pt">`+line+`</span><br>`;
         if(sentenceInfo[2]==1){
          this.currentContent+=`</div><br>`;
         }
         //this.sumLineHeights+=
         //this.cutInSentenceService.getLineHeight(line);
        if(k>=0){
              //if(this.sumLineHeights>this.pageHeight){//(this.pageHeight)){
              if(k==(this.linesPerPage+i)){
                //this.sumLineHeights=0;
                this.currentContent+='</div></div><br>';
                break;
              }
        }
      }
    }
    this.lastIndex=k;
    return k;
  }
  findSentenceInParagraphArray(lineIndex){
    var counter=0;
    for(var k=0;k<this.currentLineArray.length;k++){
      var paragraphArray=this.currentLineArray[k];
      for(var m=0;m<paragraphArray.length;m++){
        if(counter==lineIndex){
          var realIndex=paragraphArray[m];
          var isFirstSentence=(m==0);
          var isLastSentence=(m==paragraphArray.length-1);
          return [realIndex,isFirstSentence,isLastSentence,k];
        }
        counter++;
      }
    }
    return -1;
  }
  getTotalArrayLength(){
    var count=0;
    for(var i=0;i<this.currentLineArray.length;i++){
      var paragraphArray=this.currentLineArray[i];
      count+=paragraphArray.length;
    }
    return count;
  }
   //if the new document is smaller than the old one, it removes the remaining pages.
  removeRestPages(){
    var divToZoom=document.getElementById('divToZoom');
      for(var i=(divToZoom.children.length-1);i>this.currentPageIndex;i--){
        var subDivRest=<HTMLElement>divToZoom.children[i];
        subDivRest.style.width='0';
        subDivRest.style.height='0';
        subDivRest.style.margin='0';
        subDivRest.style.padding='0';
        //subDivRest.style.backgroundColor='red';
        
        //subDivRest.remove();
        //divToZoom.removeChild(subDivRest);
        //divToZoom.style.backgroundColor='blue';
      }
  }
  //dynamic paging methods
  registerEventsToAllPages(body,startPageId){
  var divToZoom=document.getElementById('divToZoom');
  for(var m=startPageId;m<divToZoom.children.length;m++){
    var element=<HTMLElement>divToZoom.children[m].children[0];
    //register touchend event to all pages and spans/sentences. 
    element.addEventListener('touchmove', (event) => {
      var element2=<HTMLElement>event.target;
      //if(element2.tagName=='DIV'){
      //}else 
      if(element2.tagName=='SPAN'){
        //element2.style.backgroundColor='red';
        element2=element2.parentElement;
      }
      //element2=element2.parentElement;
      if(element2.id.substring(0,3)=='par'){
        element2=element2.parentElement;
        //element2.style.backgroundColor='red';
      }
      var touchedPage=Number(element2.id);//Number(element2.id);
      //var roundUpPage=this.roundUpTouchedPage(touchedPage);
      //if(touchedPage==(divToZoom.children.length-1)){//&&
      if(this.isLastPageVisible(body)){
      //(divToZoom.children.length<roundUpPage)){
        this.addMorePages(body,divToZoom.children.length,2);
        //this.addPageInTheEnd(body,1+1);
      }
    }); 
   }
  }
  isLastPageVisible(body) {
    var divToZoom = <HTMLElement>document.getElementById('divToZoom');
    var firstPage = divToZoom.firstElementChild;
    var rect0 = firstPage.getBoundingClientRect();
    var lastPage = divToZoom.lastElementChild;
    var rect = lastPage.getBoundingClientRect();
    var currentOffsetHeight=divToZoom.style.height;
    var isVisible =((lastPage.getBoundingClientRect().top+70) <= 
    body.getBoundingClientRect().bottom);
    
    //2 var breakPoint = body.getBoundingClientRect().bottom 
    //- 0.4 * (body.getBoundingClientRect().top 
    //- body.getBoundingClientRect().bottom)
    //var isVisible = lastPage.getBoundingClientRect().top <= breakPoint;
    //var isVisible = (rect.top >= 0) &&
    //body.getBoundingClientRect().bottom);
     //(rect.top <=window.innerHeight);
      //window.innerHeight );
    return isVisible;
  }
  roundUpTouchedPage(touchedPage){
  var roundUpPage=Math.ceil(touchedPage / 10)*10;
  return (roundUpPage+1);
  }
}
