import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class CutInSentenceService {
  allLinesArray:Array<any>=[];
  allIdsArray:Array<any>=[];
  wordsArray:Array<String>=[];
  currentSentence='';
  indexToCut='';
  k=0;
  oldFontSize=12;
  neoFontSize=12;
  fontName='Verdana';
  currentLineHeight='200%';

  constructor() { 
  }

splitLinesArray(text:string='',maxTextWidth){
  var startPos = 0;//text.indexOf("\cf");
  //text = text.slice(startPos+3);

  //text = text.replace(/\r?\n/g, '<br />');
  //this.allLinesArray=text.split("\\n");
  //var text2=text.replace("\\\n",'');
  //var text2 = text.replace(/\\\n/g,'');
  //var text2 = text.replace(/\\\\n/g,' ');
  var paragraphArray = text.split('\\\\n');
  for(var i=0;i<paragraphArray.length;i++){
    var currentParagraph=paragraphArray[i];
    if(currentParagraph.charAt(currentParagraph.length-1)=='\\'){
      currentParagraph=currentParagraph.substr(0,currentParagraph.length-1);
    }
    this.cutInLines(currentParagraph,maxTextWidth);
  }
  return null;
}
splitLines(text,maxTextWidth) {
  if(text==' '){
    this.allLinesArray.push(' ');
    this.allLinesArray.push(' ');
  }else{
    this.splitLinesArray(text,maxTextWidth);
  }
  //this.replaceBigItems(0,maxTextWidth);
  
  return this.allLinesArray;
}
cutInLines(newText,maxTextWidth){
  var tempString='';
  var currentParagraphArray=[];
  var currentParagraphIdsArray=[];
  var p=0;
  var b=0;
  var currentText='';
  var line='';
  //console.log('old font size= '+this.fontSize);
  //console.log('new font size= '+fontSize);
  //console.log('new font size2= '+newFontSize);
  var lettersPerFont=47;
  if(this.fontName=='OpenDyslexicMono-Regular'){
    lettersPerFont=30;
  }
  var letterLimit=Math.floor(lettersPerFont*
    (this.oldFontSize/this.neoFontSize));
  while (p < newText.length){
    currentText = newText.substr(p);
    //console.log('letterLimit= '+letterLimit);
    //console.log('currentText= '+currentText);
    //console.log('b= '+b);
    //console.log('currentText.substr(letterLimit)= '+ 
    //currentText.substr(letterLimit));
    b = currentText.substr(letterLimit).indexOf(' ');
    if (b < 4) {
      line = currentText.substr(0, letterLimit + b);
    } else {
      line = currentText.substr(0, letterLimit);
      b = -1;
    }
    //if(this.k>2)break;
    p += letterLimit + b+1;
    currentParagraphArray.push(line);
    currentParagraphIdsArray.push('span'+this.k);
    this.k++;
  }
  this.allLinesArray.push(currentParagraphArray);
  this.allIdsArray.push(currentParagraphIdsArray);
}
getTextWidth2(text) {
  var spanText=document.getElementById("spanText2");
  spanText.innerHTML=text;
  return spanText.getBoundingClientRect().width;
}
getLineHeight(text) {
  var spanText=document.getElementById("spanText");
  spanText.innerHTML=text;
  return spanText.clientHeight;
}

}
