        this.handleTwoLineSentence(changedSpan,changedPage.id);
        //var isABigSentence=(this.cutInSentenceService.getTextWidth2(bigSentence)>
      //this.maxTextWidth);
        //if(secondSentence!=''){
        //this.colorAllTheRestSentences
          //(changedPage,changedSpan,secondSentence);
        //}
