import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class FindsBigSentenceService {

  constructor() { 

  }
  //This methods gets two strings and returns the position where they differ.
  findFirstDiffPos(a, b){
    //console.log('a= '+a);
    //console.log('b= '+b);
    if((a!=null)&&(b!=null)){
      var shorterLength = Math.min(a.length, b.length);
      for (var i = 0; i < shorterLength; i++){
         if (a[i] !== b[i]) return i;
      }
      if (a.length !== b.length) return shorterLength;
  
    }
    
    return -1;
  }
}
