  //CKEditor methods
  showSelectedOption(currentClassName,currentEditor,currentCommand,menuType){
    var $iframe = <HTMLIFrameElement>
    document.getElementsByClassName('cke_panel_frame')[0];
    var innerDoc = $iframe.contentDocument ? 
    $iframe.contentDocument : $iframe.contentWindow.document;
    var $elem = <HTMLElement>innerDoc.getElementsByClassName(
    currentClassName)[0];
    $elem.classList.add("optionIsActive");
    
    //$elem.style.backgroundColor='red';
    
    if(menuType=='borders'){
      if(currentCommand=='keng'){
        
        for(var k=0;k<(this.commandsArray.length-2);k++){
          
          if(this.miscOperationsService.bordersArray[k]==1){
            var currCommand=this.commandsArray[k+1];
            var command2 = currentEditor.getCommand(currCommand);
            command2.toggleState(); 
            
          }
      }
      
      

      
      
        //if(currentCommand!=currCommand){
          //var command2 = currentEditor.getCommand(currCommand);
          //command2.toggleState(); 
          
        //}
      }//else{
        if(this.miscOperationsService.bordersArray[4]==1){
          var command2 = currentEditor.getCommand('keng');
          command2.toggleState(); 
          
        }

            
      //}
      
      
    }
    var command = currentEditor.getCommand(currentCommand);
    command.toggleState(); 
      
  }
