  reloadCDText(newFontSize){
    //this.ckEditorInstance.focus();
    if(newFontSize==0){
      newFontSize=this.neoFontSize;
    }
    var textArea=document.getElementById("textArea");  
    var newText=textArea.textContent;
    newText=newText.split('\\strokec4 ')[1];
    var body = this.ckEditorInstance.editable().$;    
    this.lineSeparatorService.loadTextContent(body,newText,
      0,this.maxTextWidth);
    this.addZoomPossibility();   
  }
