import { Injectable } from '@angular/core';
import {CutInSentenceService} from "../services/cut-in-sentence.service";

@Injectable({
  providedIn: 'root'
})
export class MiscOperationsService {
  bordersArray=[0,0,0,0,0,0];
  colorNumberMode=0;
  firstTime=1;
  testSwitch=0;
  currentRgbButtonIcon='rgb.png';
  rbFlag=0;
  rgbFlag=0;
  
  constructor(private cutInSentenceService:CutInSentenceService) { 

  }
  colorNumbers(){
    //currentColor=1;
    var divToZoom=document.getElementById('divToZoom');
    var childIndex=0;
    //if(this.testSwitch==0){
      for(var i=0;i<divToZoom.children.length;i++){
        var page=<HTMLElement>divToZoom.children[i].children[0];
        for(var k=0;k<page.children.length;k++){
          var childDiv=<HTMLElement>page.children[k];
          if(childDiv.tagName=='DIV'){
            for(var m=0;m<childDiv.children.length;m++){
              var sentence=<HTMLElement>childDiv.children[m];
              if(sentence.tagName=='SPAN'){
                if((this.colorNumberMode==0)&&(this.firstTime==1)){
                  this.findAllNumbersAndColorThem(sentence);
                }else{
                  if(sentence.children.length>0){
                    var childSpanArray=sentence.children;
                    var colorIndex=0;
                    var colorArray=['green','red','blue'];
                    for(var w=0;w<childSpanArray.length;w++){
                      var childSpan=<HTMLElement>childSpanArray[w];
                      if(childSpan.tagName=='SPAN'){
                        if(this.firstTime==0){
                          if(this.colorNumberMode==1){
                            childSpan.style.color='black';
                          }else{
                            if(colorIndex==3){
                              colorIndex=0;
                            }
                            childSpan.style.color=colorArray[colorIndex];
                            colorIndex++;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
            childIndex++;
          }
        }
      }
      if(this.firstTime==1){
        this.firstTime=0;
      }
      if(this.colorNumberMode==1){
        this.colorNumberMode=0;
      }else{
        this.colorNumberMode=1;
      }
    //}else if(this.testSwitch==1){
      //this.underlineWithColors(1);
    //}
    this.testSwitch++;
  }
  findAllNumbersAndColorThem(sentence){
    var sentenceText=sentence.innerHTML;
    var numberIndicesArray=[];
    for(var m=0;m<sentenceText.length;m++){
      var currentChar=sentence.textContent.charAt(m);
      if(!isNaN(parseInt(currentChar))){
        numberIndicesArray.push(m);
      }
    }
    if(numberIndicesArray.length>0){
      this.addSpansToSentence(sentence,numberIndicesArray);
    }
  }
  //This method gets an array ,with all the indexes of the sentence,
  //where numbers exist and inserts spans on these indexes using 
  //specific colors according to a pattern.
  addSpansToSentence(sentence,currentNumberArray){
    var newSentence='';
    var counter=-1;
    var currentSentence=sentence.textContent;
    var colorArray=['green','red','blue'];
    var colorIndex=0;
    for(var i=0;i<currentNumberArray.length;i++){
        var currentNumberIndex=currentNumberArray[i];
        newSentence+=currentSentence.substr((counter+1),
        (currentNumberIndex-counter-1));
        
        var currentNumber=sentence.innerHTML.charAt(currentNumberIndex);
        if(colorIndex==3){
            colorIndex=0;
        }
        
        var newCharSpan = 
        '<span style="color:'+colorArray[colorIndex]+'">' +
        currentNumber+'</span>';
        //currentNumber.fontcolor(colorArray[colorIndex]);
        colorIndex++;
        if(sentence.innerHTML.charAt(currentNumberIndex+1)=='.'){
          
        }else{
          if(currentNumberArray[i+1]!=(currentNumberIndex+1)){
            colorIndex=0;
          }
        }
        newSentence+=newCharSpan;
        counter= currentNumberIndex;
        //var newSentence=sentenceText.substr(0, m)+newCharSpan+
        //sentenceText.substr(m + 1);
        //var smallSpan = document.createElement('span');
        //smallSpan.innerHTML = newCharSpan;
        //smallSpan.style.color='red';
        //sentence.appendChild(smallSpan);

    }
    newSentence+=currentSentence.substr(counter + 1);
    sentence.innerHTML=newSentence;
  }
  setMargins(){
    this.applyBorder();   
  }
  applyBorder(){
    var divToZoom=document.getElementById('divToZoom');
    for(var i=0;i<divToZoom.children.length;i++){
        var page=<HTMLElement>divToZoom.children[i].children[0];
        for(var k=0;k<page.children.length;k++){
          var childDiv=<HTMLElement>page.children[k];
          if(childDiv.tagName=='DIV'){
            this.setCurrentBorder(childDiv);
          }
        }
    } 
  }
  setCurrentBorder(childDiv){
    var bordersArray=this.bordersArray;
    childDiv.style.borderLeft=((bordersArray[0]==1)||(bordersArray[5]==1))?'solid green':'';
    childDiv.style.borderRight=((bordersArray[1]==1)||(bordersArray[5]==1))?'solid red':'';
    childDiv.style.borderTop=((bordersArray[2]==1)||(bordersArray[5]==1))?'solid green':'';
    childDiv.style.borderBottom=((bordersArray[3]==1)||(bordersArray[5]==1))?'solid red':'';
    //childDiv.style.border='solid transparent';
    
  }
  changeLineColors(twoOrThreeColors){
    //currentColor=1;
    var rbButtonIcon=<HTMLElement>document.getElementsByClassName
    ('cke_button__rbchangecolorbutton_icon')[0];
    var rgbButtonIcon=<HTMLElement>document.getElementsByClassName
    ('cke_button__rgbchangecolorbutton_icon')[0];
    if(twoOrThreeColors==1){
      console.log('this.rbFlag= '+this.rbFlag);
      if(this.rbFlag==0){
        setTimeout(() => {
          rbButtonIcon.style.backgroundImage = "url('/assets/rb-selected.png')"
          rgbButtonIcon.style.backgroundImage = "url('/assets/rgb.png')"  
        }, 100);
        this.rbFlag=1;
        this.rgbFlag=0;
      }else{
        setTimeout(() => {
          rbButtonIcon.style.backgroundImage = "url('/assets/rb.png')"
        }, 100);
        //rgbButtonIcon.style.backgroundImage = "url('/assets/rgb.png')"  
        this.rbFlag=0;
        twoOrThreeColors=0;
        console.log('rb0');
      }
    }else{
      console.log('this.rgbFlag= '+this.rgbFlag);
      if(this.rgbFlag==0){
        setTimeout(() => {
          rbButtonIcon.style.backgroundImage = "url('/assets/rb.png')"
          rgbButtonIcon.style.backgroundImage = "url('/assets/rgb-selected.png')"
        }, 100);
        this.rgbFlag=1;
        this.rbFlag=0;

      }else{
        //rgbButtonIcon.style.backgroundImage = "url('/assets/rb.png')"
        setTimeout(() => {
          rgbButtonIcon.style.backgroundImage = "url('/assets/rgb.png')"  
        }, 100);
        this.rgbFlag=0;
        twoOrThreeColors=0;
        console.log('rgb0');

      }        
    }
    //event.style.display='none';
    var currentButton=<HTMLElement>event.target;
    var divToZoom=document.getElementById('divToZoom');
    var childIndex=0;
    for(var i=0;i<divToZoom.children.length;i++){
      var page=<HTMLElement>divToZoom.children[i].children[0];
      for(var k=0;k<page.children.length;k++){
        var childDiv=<HTMLElement>page.children[k];
        if(childDiv.tagName=='DIV'){
          for(var m=0;m<childDiv.children.length;m++){
            var sentence=<HTMLElement>childDiv.children[m];
            if(sentence.tagName=='SPAN'){
              switch(twoOrThreeColors){
                case 0:{
                  sentence.style.color='black';
                  break;
                }
                case 1:{
                  var sentenceColor='blue';
                  if((childIndex+1)%2==1){
                    sentenceColor='red';
                  }
                  sentence.style.color=sentenceColor;
                  break;
                }
                case 2:{
                  var sentenceColor='blue';
                  if((childIndex+1)%3==1){
                    sentenceColor='green';
                  }
                  else if((childIndex+1)%3==2){
                    sentenceColor='red';
                  }            
                  sentence.style.color=sentenceColor;
                  break;
                }
              }
              childIndex++;
            }
          }
        }
      }
    }
  }
  addMoreClasses(ckeditor){
    ckeditor.addCommand('frame1', {});
    ckeditor.addCommand('frame2', {});
    ckeditor.addCommand('frame3', {});

    ckeditor.addCommand('font1', {});
    ckeditor.addCommand('font2', {});
    
    ckeditor.addCommand('lineDistance1', {});
    ckeditor.addCommand('lineDistance2', {});
    ckeditor.addCommand('lineDistance3', {});

    ckeditor.addCommand('keng', {});
    ckeditor.addCommand('lenks', {});
     ckeditor.addCommand('uewen', {});
     ckeditor.addCommand('riets', {});
     ckeditor.addCommand('ennen', {});
     ckeditor.addCommand('all', {});
    for(var k=10;k<21;k+=2){
      ckeditor.addCommand('fontSize'+k, {});
    }
  }
  fillFontSizeArray(currentInstance,_this){
    var fontSizeArray=[];
    for (const i of [ 10, 12 ,14, 16, 18, 20]) {
      fontSizeArray.push({
            name: i + 'pt',
            command: 'fontSize'+i,
            onClick: function() {              
              //currentInstance.className,
              //currentInstance.editor,currentInstance.command);
              //currentInstance.cutInSentenceService.fontSize=i;
              //this.currentFontSize=i;
              currentInstance.neoFontSize=i;
              currentInstance.cutInSentenceService.neoFontSize=i;
              currentInstance.neoFontSizeClass = this.className;
              currentInstance.reloadCDText(i);
            }
        });
    }
    return fontSizeArray;
  }  
}
